<template>
  <div class="wrapper">
    <img class="wrapper__img" src="http://www.dell-lee.com/imgs/vue3/user.png" alt="">
    <div class="wrapper__input">
      <input placeholder="用户名" type="text" class="wrapper__input__content">
    </div>
    <div class="wrapper__input">
      <input placeholder="密码" type="password" class="wrapper__input__content">
    </div>
    <div class="wrapper__input">
      <input placeholder="确认密码" type="password" class="wrapper__input__content">
    </div>
    <div class="wrapper__register" @click="handleRegister">注册</div>
    <div class="wrapper__links" @click="handleLoginClick">已有账号去登录</div>
  </div>
  <Toast v-if="show" :message="toastMessage" />
</template>

<script>
import { reactive, toRefs } from 'vue'
import { useRouter } from 'vue-router'
import Toast, { useToastEffect } from '../../components/Toast.vue'
import { post } from '../../utils/request'

const useRegisterEffect = showToast => {
  const router = useRouter()
  const data = reactive({
    username: '',
    password: '',
    ensurement: ''
  })
  const handleRegister = async () => {
    try {
      const result = await post('/api/user/register', {
        username: data.username,
        password: data.password
      })
      if (result.errno === 0) {
        router.push({ name: 'Login' })
      } else {
        showToast('注册失败')
      }
    } catch (e) {
      showToast('请求失败')
    }
  }
  const { username, password, ensurement } = toRefs(data)
  return { handleRegister, username, password, ensurement }
}

export default {
  name: 'Register',
  components: { Toast },
  setup () {
    const router = useRouter()
    const { show, toastMessage, showToast } = useToastEffect()
    const { username, password, ensurement, handleRegister } = useRegisterEffect(showToast)
    const handleLoginClick = () => {
      router.push({ name: 'Login' })
    }
    return { show, toastMessage, username, password, ensurement, handleLoginClick, handleRegister }
  }
}
</script>

